* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
}

body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  width: 200px;
  height: 231px;
}

.person__photo {
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  display: flex;
  position: relative;
}

.person__photo-img {
  border-radius: 200px;
  width: 200px;
  animation-duration: 1s;
  animation-delay: 1s;
}

.person__photo-rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 100px;
  right: 10px;
}

.person__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.person__details-name {
  color: #2f4f4f;
  font-size: 20px;
}

.person__details-extra {
  font-size: 18px;
  font-weight: 400;
  display: none;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
  width: 220px;
  height: auto;
  transform: translate(-10px);
}

.person:hover .person__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.person:hover .person__details-extra {
  display: flex;
}

@keyframes changeBorder {
  from {
    border-radius: 200px;
  }

  to {
    border-radius: 60px;
  }
}

.person:hover .person__photo-img {
  animation: 1s forwards changeBorder;
}
